import React, { useState } from 'react';
import ToDolists from "./ToDolist";

const App=()=>{
  //step-3-now using hooks for adding data using js
  const[inputList, setInputList]=useState("") //useState is ahookk(function that allows you to have state variable in funcrional components.U pass the initial state to this function, and it returns a variable with the ci]urrebt state value (not necessarily the initial state)and another Function to update this value

  //step-5-creating hooks for adding data in array while clicking on button 
  const[Items, setItems]= useState([]);//ye jo array banaya hai wo basically items ko initially array ki tarah define kar dega to uska use sake

  const itemEvent= (event) =>{
    setInputList(event.target.value);//user jab bhi ku6 likhega to ye setinputlist me add ho jayega aur bad me usestate me add ho jayega. fir wo value inputlist me jake current add kar degi
  }
  
  const listOfItems=() =>{
    setItems((oldItems)=> {
      return [...oldItems, inputList] //ye '...' spread operator hai usse useState ke andr k sare data managava skate hai means jitne aye hai utne currently
    });
    setInputList('');
  };

  const deleteItems=(id)=>{
    console.log("deleted");

    setItems((oldItems)=>{
      return oldItems.filter((arrElm, index)=>{
        return index !==id;
        
      })
    })
  }

  //step-1-making basic html for code
  return<>
  
    <div className="main_div">
      <div className="center_div">
        <br/>
        <h1>To-Do List</h1>
        <br/>
        <input type="text" placeholder="Add Items"
        value={inputList}
        onChange={itemEvent} />
        <button onClick={listOfItems}>
          +
        </button>

        <ol>
          {/* <li>
            {inputList}
          </li> */}
          {/* to show array data we use map methos in react so we are making list for array data using map method */}
          
         { Items.map( (itemVal, index)=> {
            // return <li>{itemVal}</li>
            return < ToDolists 
            key={index} 
            id={index} 
            text={itemVal}
            onSelect={deleteItems}
             />;
            
          } ) }{/* maps k 4 attribute hote hai currentvalue, index num of curr.val., which array u r using, this  */}
        {/* we have to use curley braces for this snippet bcz jsx file doesn't understand code with html tha's why */}


        </ol>
      </div>
    </div>
  </>;

}

export default App;
